"use client";
import { TimetableResult } from "@/types/timetable";
import { useEffect, useState } from "react";
import LoadingSpinner from "@/components/LoadingSpinner";
import TimetableCard from "@/components/TimetableCard";

export default function GeneratorPage() {

  const [parsed, setParsed] = useState<TimetableResult | null>(null);
    const [loading, setLoading] = useState(false);
const [versions, setVersions] = useState<TimetableResult[]>([]);

  useEffect(() => {
    const p = localStorage.getItem("parsedTimetable");
if (p) setParsed(JSON.parse(p) as TimetableResult);
}, []);

  const generate = async () => {
    if (!parsed) return;
    setLoading(true);

    const res = await fetch("/api/generate", {
      method: "POST",
      body: JSON.stringify({
        parsed: parsed.parsed,
        constraints: parsed.meta || {},
      }),
    });

    const data = await res.json();
    setVersions(data.versions);

    setLoading(false);
  };

  return (
    <div className="max-w-4xl mx-auto p-6">
      <h1 className="text-3xl font-bold mb-4">Generate Timetable</h1>

      {parsed && (
        <pre className="bg-gray-100 p-3 rounded mb-4 max-h-40 overflow-auto">
          {JSON.stringify(parsed.parsed.rows.slice(0, 10), null, 2)}
        </pre>
      )}

      <button onClick={generate} className="bg-indigo-600 text-white px-6 py-2 rounded">
        Generate Timetables
      </button>

      {loading && <LoadingSpinner />}

      <div className="grid grid-cols-1 md:grid-cols-3 gap-4 mt-6">
        {versions.map(v => (
          <TimetableCard key={v.id} result={v} />
        ))}
      </div>
    </div>
  );
}
